*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0; }

html {
  font-size: 80%; }

@media (max-width: 650px) {
  html {
    font-size: 62.5%; } }

body {
  box-sizing: border-box;
  font-family: 'Roboto', Verdana, sans-serif;
  color: #343a40;
  background-color: whitesmoke; }

h1 {
  margin-top: 1.5rem;
  font-size: 3rem;
  text-align: center; }

p {
  text-align: center; }
